:root{--main-color: #0f292f;--second-color: #fef9c7;--gold-color: #ecc63e;--purple-color: #CB2D6F;--green-color: #14a098}body{margin:0 auto;font-family:-apple-system, BlinkMacSystemFont, "Comic Sans MS", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#932a35}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.icon{width:23px;margin:0 15px}.nav{margin-top:20px;width:200px;padding:10px 10px 10px 10px;height:750px;background-color:var(--main-color)}.nav__hr{width:60%;margin:30px auto 0}.nav__items{margin-top:30px}.nav__item a{color:#FFFFFF;font-size:30px;line-height:44px;text-decoration:none}.nav__item a:hover{color:var(--gold-color)}.nav__item a.activeLink{color:var(--gold-color)}.nav__item_last a{color:#FFFFFF;font-size:30px;line-height:100px;text-decoration:none}.nav__item_last a:hover{color:var(--gold-color)}.nav__item_last a.activeLink{color:var(--gold-color)}

.audio{padding:20px 20px}.audio_title{font-size:30px;color:white;margin:20px 170px}.audio_track{margin:20px 150px;width:500px;outline:none}.audio_item{margin:10px 0;width:300px}

.sidebar{padding:10px 20px;margin:20px 10px;width:1000px;border:1px solid darkgray;border-radius:20px;min-height:750px}.sidebar__title{margin-bottom:30px;text-align:center;font-size:36px;font-weight:bold}.sidebar__items{display:flex;text-align:center;justify-content:center;flex-wrap:wrap;border:1px solid darkgray;border-radius:20px;margin:0 10px;padding:30px 10px;background-color:var(--second-color)}.sidebar__item{padding:7px 7px;width:100px;height:100px;margin:7px 7px}.sidebar__item__img{width:70px}.sidebar__item__img__online{width:50px;border:10px solid #16a085;border-radius:100%}.sidebar__item__name{color:#47525E;font-weight:bold}.sidebar__item__url{text-decoration:none}

.news__wrapper{padding:10px 10px;margin:20px 10px;width:1000px;border:1px solid darkgray;border-radius:20px;min-height:750px;background-color:var(--second-color)}.news__navbar__items{display:flex;justify-content:center;margin:20px}.news__navbar__item{width:150px;height:30px;margin:5px;font-size:18px;background-color:var(--gold-color);outline:none;border:1px solid darkgray;border-radius:7px}.news__items{width:700px;margin:0 auto}.news__item{text-align:center;margin-top:20px;padding:15px 25px;border:1px solid darkgray;border-radius:10px;background-color:white}.news__item img{width:100%;margin-top:20px}

.users{padding:10px 20px;margin:0 auto}.users__pagination{width:500px;height:50px;padding:0 20px;display:flex;justify-content:space-around;margin:0 auto;align-items:center;background-color:#fffe0069;color:var(--main-color);border:1px solid darkgrey;border-radius:20px;cursor:pointer}.users__pagination_selected{font-weight:bold;font-size:20px}.users__wrapper{margin:15px 20px}.users__item{display:flex;width:700px;margin:10px 40px;justify-content:space-between;border:1px solid darkgrey;border-radius:15px;padding:15px 20px;background-color:var(--second-color)}.users__avatar{width:20%;text-align:center}.users__avatar__img{width:70px}.users__button{margin:10px auto 0 auto;width:80px;height:35px;color:black;font-weight:bold;background-color:var(--gold-color);border:1px solid darkgrey;border-radius:10px;outline:none;font-size:13px;cursor:pointer}.users__button_follow{margin:10px auto 0 auto;width:80px;height:35px;font-weight:bold;border:1px solid darkgrey;border-radius:10px;outline:none;font-size:13px;color:black;background-color:var(--green-color);cursor:pointer}.users__info{width:60%;padding:10px 60px;line-height:35px}.users__info__fullName{font-size:28px;font-weight:bold}.users__info__status{font-style:italic}.users__locate{width:20%;margin:auto auto;line-height:30px;text-align:center}.users__locate_country{font-size:25px}.users__butWrap{margin-top:40px;text-align:center}.users__butWrap__showMoreBut{width:200px;height:50px;background-color:var(--main-color);color:white;font-size:22px;font-weight:bold;align-items:center;border:1px solid darkgrey;border-radius:15px;outline:none}.preloader{position:absolute;top:50%;left:50%}

.logoutButton{width:50px;margin:0 0 0 40px;cursor:pointer}.app__header{display:flex;margin-top:10px;width:100%;height:120px;align-items:center;background-color:var(--main-color)}.app__header_content{width:1200px;margin:0 auto}.app__logo{position:absolute;width:200px;margin-left:320px}.app__login__block{display:flex;margin-left:auto;padding:0 350px}.app__login__block_button{width:50px}.app__login__block_wrapper{display:flex}.app__login__block_login{text-transform:uppercase;font-size:24px;font-weight:bold;margin:auto 0;cursor:pointer;color:var(--purple-color)}.app__login__block_img{width:50px;margin:0 20px}

.login_form{border:2px solid var(--green-color);border-radius:25px;padding:80px 110px 110px 110px;background:var(--main-color);color:white;margin:auto auto}.login_form_error{background-color:#CB2D6F}.login_form_authField{margin:10px 0;border:1px solid black;border-radius:15px;width:275px;height:30px;padding-left:15px;outline:none;background:var(--second-color)}.login_form_authField:focus{background:white}.login_form_checkbox{margin:10px 10px 10px 20px}.login_form_buttons{display:flex;margin-top:10px}.login_form_loginButton{background:var(--purple-color);color:white;font-size:18px;font-weight:bold;width:100px;height:40px;border-radius:7px;margin-right:10px;cursor:pointer;outline:none}.login_form_forgotButton{background:var(--green-color);color:white;font-size:18px;width:180px;height:40px;border-radius:7px;margin-right:10px;cursor:pointer;outline:none}.login_form_title{text-align:center;color:var(--purple-color);font-size:40px}

